import * as React from 'react';
import {Overlay} from '@here/hds-overlay';

// eslint-disable-next-line no-new
new Overlay();

interface HDSOverlayProperties extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
}

const HDSOverlay = React.forwardRef((props: HDSOverlayProperties, ref) => {
  const {children, className, ...rest} = props;
  return (
    <hds-overlay ref={ref} {...(className && {class: className})} {...rest}>
      {children}
    </hds-overlay>
  );
});

export default HDSOverlay;
